// import Papa from "papaparse";
export const POKEMON_TYPES = ["Normal", "Fighting", "Flying", "Poison", "Ground", "Rock", "Bug", "Ghost", "Steel", "Fire", "Water", "Grass", "Electric", "Psychic", "Ice", "Dragon", "Dark", "Fairy"];
export const POKEMON_STATS_CONCISE = ["HP", "Att", "Def", "SAtt", "SDef", "Spd"];
export const POKEMON_STATS_FULL = {"Any": "Any", "HP": "Hit Points", "Att": "Attack", "Def": "Defense", "SAtt": "Special Attack", "SDef": "Special Defense", "Spd": "Speed"}
export const POKEMON_SORT_CRITERIA = {"Number": "Number", "totalStats": "Total Stats", "hpStat": "Hit Points", "attStat": "Attack", "defStat": "Defense", "sattStat": "Special Attack", "sdefStat": "Special Defense", "spdStat": "Speed"}

let images = [];
let images_shiny = [];
let type_images = {};

const stringPath = (i, shinystr) => {
    if (i < 10) {
        return "./assets/images/pokemon_00" + i.toString() + shinystr + ".png";
    } else if (i < 100) {
        return "./assets/images/pokemon_0" + i.toString() + shinystr + ".png";
    } else {
        return "./assets/images/pokemon_" + i.toString() + shinystr + ".png";
    }
}

for (let i = 0; i < POKEMON_TYPES.length; i+=1) {
  const type = POKEMON_TYPES[i].toLowerCase();
  type_images[POKEMON_TYPES[i]] = {id: type, src: "./assets/images/" + type + ".gif", description: type};
}

for (let i = 1; i <= 400; i+=1) {
    images.push({id: i, src: stringPath(i, ""), description: "pokemon" + i.toString()})
    images_shiny.push({id: i, src: stringPath(i, "_shiny"), description: "pokemon_shiny" + i.toString()})
}
export const POKEMON_IMAGES = images;
export const POKEMON_IMAGES_SHINY = images_shiny;
export const POKEMON_TYPE_IMAGES = type_images;

